import {Component, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {RefreshHandler} from "@service/global-refresh.service";
import {VsanUiUtils} from "@util/vsan-util";
import {ModalBuilder} from "@util/modal-builder";
import {Logger} from "@util/logger";
import {VsanHealthPropertyProvider} from "@generated/vsan-health-property-provider";
import {VsanPropertyProvider} from "@generated/vsan-property-provider";
import {ExternalProxySettingsConfig} from "@generated/external-proxy-settings-config";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {ScreenReaderAnnouncer} from "@service/screen-reader-announcer.service";

@Component({
   templateUrl: "internet-connectivity.html",
   styleUrls: ["internet-connectivity.scss"],
   selector: "vsan-internet-conn-settings",
})
export class InternetConnectivityComponent implements OnInit {

   private clusterRef: ManagedObject;

   alertText: string;
   hasEditPermissions: boolean = false;
   status: string;
   proxy: string;
   userName: string;

   @ScreenReaderAnnouncer()
   srOnlyMessage: string;

   constructor(private healthProvider: VsanHealthPropertyProvider,
         private vsanPropertyProvider: VsanPropertyProvider,
         private permissionService: PermissionService) {
   }

   ngOnInit() {
      this.fetchData();
   }

   @RefreshHandler
   async fetchData() {
      this.alertText = null;
      this.status = VsanUiUtils.getString("vsan.loading");
      this.proxy = VsanUiUtils.getString("vsan.loading");
      this.userName = VsanUiUtils.getString("vsan.loading");

      const vcRef: ManagedObject = ManagedObject.contextObject;
      try {
         this.clusterRef = await this.vsanPropertyProvider.getAnyVsanCluster(vcRef);
         this.permissionService.hasVcPermissions(ManagedObject.contextObject, [Privileges.EDIT_CLUSTER()])
               .then(result => this.hasEditPermissions = result)
               .catch(error => Logger.error("Cannot retrieve permissions: " + error));
         const proxySettings = await this.healthProvider.getExternalProxySettings(this.clusterRef);
         this.status = this.populateStatus(proxySettings);
         this.proxy = this.populateProxy(proxySettings);
         this.userName = this.populateUserName(proxySettings);

         this.srOnlyMessage = VsanUiUtils.getString("vsan.screenReader.message.page.loaded",
               VsanUiUtils.getString("vsan.manage.externalProxySettings.title"));
      } catch (error) {
         Logger.error(`Error getting proxy settings: ${error}`);
         this.handleError(error);
      }
   }

   private handleError = (err: string) => {
      this.alertText = err;
      this.status = VsanUiUtils.getString("vsan.na.label");
      this.proxy = VsanUiUtils.getString("vsan.na.label");
      this.userName = VsanUiUtils.getString("vsan.na.label");
   };

   private populateStatus(externalProxyData: ExternalProxySettingsConfig): string {
      if (!externalProxyData) {
         return VsanUiUtils.getString("vsan.na.label");
      }
      return externalProxyData.enableInternetAccess ?
            VsanUiUtils.getString("vsan.manage.externalProxySettings.internetAccess.status.enabled")
            : VsanUiUtils.getString("vsan.manage.externalProxySettings.internetAccess.status.disabled");
   }

   private populateProxy(externalProxyData: ExternalProxySettingsConfig): string {
      if (!externalProxyData || !externalProxyData.hostName ||
               (!externalProxyData.port && externalProxyData.port !== 0)) { // 0 is a valid port number.
         return VsanUiUtils.getString("vsan.na.label");
      }

      const hostName: string = externalProxyData.hostName;
      const port: string = "" + externalProxyData.port;

      const autoDetectTxt: string = externalProxyData.isAutoDiscovered ?
            VsanUiUtils.getString("vsan.manage.externalProxySettings.proxyType.autoDetected")
            : VsanUiUtils.getString("vsan.manage.externalProxySettings.proxyType.userConfigured");

      return VsanUiUtils.getString("vsan.manage.externalProxySettings.proxy.summary.pattern",
            hostName, port, autoDetectTxt);
   }

   private populateUserName(externalProxyData: ExternalProxySettingsConfig): string {
      if (!externalProxyData || !externalProxyData.userName) {
         return VsanUiUtils.getString("vsan.na.label");
      }

      return externalProxyData.userName;
   }

   editSettings = (): void => {
      if (!this.clusterRef) {
         return;
      }
      new ModalBuilder("internetConnectivity/editConfig")
            .setSize(580, 420)
            .open({
               clusterRef: this.clusterRef,
            })
            .then((statusChanged: boolean) => {
               if (statusChanged) {
                  this.fetchData();
               }
            });
   };
}