import {ChangeDetectorRef, Component, OnInit, ViewChild} from "@angular/core";
import {ExternalProxySettingsConfig} from "@generated/external-proxy-settings-config";
import {VsanHealthPropertyProvider} from "@generated/vsan-health-property-provider";
import {VsanHealthMutationProvider} from "@generated/vsan-health-mutation-provider";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {FormBuilder, FormControl, FormGroup} from '@angular/forms';
import {ClrForm} from "@clr/angular";
import {ProxySetting} from "@component/proxy-settings/proxy-inputs.component";

@Component({
   templateUrl: "edit-internet-connectivity-config.html",
   styleUrls: ["edit-internet-connectivity-config.scss"],
   selector: "vsan-edit-internet-connectivity-config",
})
export class EditInternetConnectivityComponent implements OnInit {

   readonly ENABLE_INTERNET_CONTROL_NAME: string = "enableInternet";
   readonly PROXY_CONTROL_NAME: string = "proxy";

   private originalSpec: ExternalProxySettingsConfig;
   private clusterRef: ManagedObject;

   busy: boolean = true;
   alertMessages: string[];

   internetSettings: FormGroup;

   @ViewChild(ClrForm)
   internetForm: ClrForm;

   constructor(private formBuilder: FormBuilder,
               private healthProvider: VsanHealthPropertyProvider,
               private healthMutationProvider: VsanHealthMutationProvider,
               private changeDetectorRef: ChangeDetectorRef) {}

   ngOnInit(): void {
      this.busy = true;
      this.clusterRef = VsanUiUtils.getModalContext().clusterRef;
      this.healthProvider.getExternalProxySettings(this.clusterRef)
            .then(this.handleSettings)
            .catch(this.handleError);
   }

   // Assigns correct change detection and prevents "ExpressionChangedAfterItHasBeenCheckedError" exception.
   // The exception was causing the blockButton to not get correct value when toggling the forms
   ngAfterViewChecked(): void {
      this.changeDetectorRef.detectChanges();
   }

   private handleSettings = (currentSettings: ExternalProxySettingsConfig) => {
      this.busy = false;
      this.originalSpec = currentSettings;

      if (this.hasHostname(currentSettings.hostName)) {
         this.internetSettings = this.formBuilder.group({
               [this.ENABLE_INTERNET_CONTROL_NAME]: new FormControl(currentSettings.enableInternetAccess),
               [this.PROXY_CONTROL_NAME]: new FormControl(new ProxySetting(
                     true,
                     currentSettings.hostName,
                     currentSettings.port,
                     currentSettings.userName,
                     currentSettings.password)),
         });
      } else {
         this.internetSettings = this.formBuilder.group({
               [this.ENABLE_INTERNET_CONTROL_NAME]: new FormControl(currentSettings.enableInternetAccess),
               [this.PROXY_CONTROL_NAME]: new FormControl(new ProxySetting()),
         });
      }
   };

   private handleError = () => {
      this.busy = false;
      this.alertMessages = [VsanUiUtils.getString("vsan.common.error.dataExtraction")];
   };

   confirm = () => {
      this.alertMessages = null;

      // For a special case: when enabling checkboxes and click APPLY button directly, the status of
      // custom form control is not correct, we need to call updateValueAndValidity() for it first.
      this.internetSettings.get(this.PROXY_CONTROL_NAME).updateValueAndValidity();
      if (this.internetSettings.invalid) {
         // Display input field error message when there is no proxy setting change.
         this.internetForm.markAsTouched();
         return;
      }

      const proxyConfig: ProxySetting = this.internetSettings.get(this.PROXY_CONTROL_NAME).value;
      const applyProxySetting: boolean = !!proxyConfig && this.getEnableInternet() &&
            this.internetSettings.get(this.PROXY_CONTROL_NAME).value.enableProxy;
      const spec: ExternalProxySettingsConfig = {
         isAutoDiscovered: false,
         enableInternetAccess: this.getEnableInternet(),
         hostName: applyProxySetting && proxyConfig.hostname ? proxyConfig.hostname : null,
         port: applyProxySetting && proxyConfig.port ? proxyConfig.port : 0,
         userName: applyProxySetting && proxyConfig.username ? proxyConfig.username : null,
         password: applyProxySetting && proxyConfig.password ? proxyConfig.password : null,
      };

      const promises: Promise<any>[] = [this.healthMutationProvider.setTelementryConfig(this.clusterRef, spec)];
      if (this.hasHostname(this.originalSpec.hostName) &&
            !this.internetSettings.get(this.PROXY_CONTROL_NAME).value.enableProxy) {
         promises.push(this.healthMutationProvider.clearTelementryConfig(this.clusterRef));
      }
      this.busy = true;
      Promise.all(promises).then(() => this.close(true)).catch(this.showAlert);
   };

   close = (statusChanged: boolean = false) => {
      this.busy = false;
      VsanUiUtils.closeModalDialog(statusChanged);
   };

   private showAlert = (error: string) => {
      this.busy = false;
      this.alertMessages = [error];
   };

   private hasHostname = (hostName: string): boolean => {
      return (typeof hostName !== 'undefined') && hostName !== null && hostName.length > 0;
   };

   getEnableInternet(): boolean {
      return this.internetSettings && this.internetSettings.get(this.ENABLE_INTERNET_CONTROL_NAME).value;
   }

   get blockApplyButton(): boolean {
      return this.busy
            || this.alertMessages && this.alertMessages.length > 0
            || (this.internetSettings && this.internetSettings.touched && this.internetSettings.invalid);
   }
}
